body {
  margin: 0px;
  background-color: #181820;
  color: #E0CCB3;


}
header{
  text-decoration: none;
  background-color: #181820;
  color: #E0CCB3;
}

.app-container {
    font-family: sans-serif;
    height: calc(100vh - 34px - 50px - 64px);
}
h1{
    text-align: center;
    margin: 20px;
    background-color: #AD2610;
    border-radius: 5px;
}
.app-footer {
    background: #181820;
    padding: .5rem 2rem;
    text-align: right;
    bottom: 0px;
    position: absolute;
    width: 100%;
}

.app-footer__message {
    text-decoration: none;
    color:#E0CCB3; 
    font-family: sans-serif;
}

.Instagram{
    font-size: 25px;
    text-decoration: none;
    color:#E0CCB3;
}

.app-footer__message:hover {
    text-decoration: none;
    color:#E0CCB3; 
}
a{
    text-decoration: none;
}


.app-menu__list {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    padding-left: 0px;
}

.app-menu__item {
    list-style: none;
    display: -webkit-inline-flex;
    display: inline-flex;
    -webkit-align-items: center;
            align-items: center;
}

.app-menu__link {
    color: #E0CCB3;
    font-family: sans-serif;
    margin-right: 1rem;
    text-decoration: none;
}

.app-menu__link:hover {
    text-decoration: none;
    color:#E0CCB3;
}
.app-menu__logo{
    height: 90px;
    width: 90px;
    margin: 20px;  
}
.app-header {
    -webkit-align-items: center;
            align-items: center;    
    font-size: 30px;     
}

h3{
    text-align: left;
    text-align: initial;
    margin: 20px;
    padding-left: 30px;
    margin-bottom: 0px;
}
.box{
    background-color: #2D2D32;
    padding: 1em;
    width: 95%;
    margin: 0 auto;
    border-radius: 1em;
}
form div + div {
    margin-top: 0.5em;
}
label{
    font-size: 30px;
    display: inline-block;
    width: 125px;
    text-align: center;
}
input, textarea {
    font: 20px sans-serif;
    width: 80%;
    margin: 0 auto;
    box-sizing: border-box;
    border: 1px solid #999;
    border-radius: 0.25em;
}
.save{
    font-size: 30px;
    text-decoration: none;
    color:#E0CCB3;
}
button{

   background-color: #2D2D32;
   border: none;
   text-decoration: none;
   display: inline-block;

}
div.button{
    display: inline-block;
    width: 100%;
    text-align: end;
}
